




























import {Vue, Component, Emit} from 'vue-property-decorator'

const md5 = require('md5');

@Component
export default class Login extends Vue {
  $axios: any;
  username: string = ''
  password: string = ''

  created(): void {
  }

  async login() {
    try {
      const login_data = {
        username: this.username,
        password: md5(this.password)
      }
      let res = await this.$axios.post(`/api/user/login`, login_data)
      console.log('res:', res)
      // this.$message({
      //   type: 'success',
      //   message: '登录成功'
      // })
      alert('登录成功')

      this.$emit('hideform')
      await this.$store.dispatch('user/get_user_info')
      location.reload()
    } catch (e) {
      console.log('e:', e)
    }
    return false
  }

  @Emit('hideform')
  hide() {
  }
}
